:root {
  --font-inter: "Inter", sans-serif;
  --font-lexendDeca: "Lexend Deca", sans-serif;
}

:root {
  --main-bg-color: hsl(233, 47%, 7%);
  --card-bg-color: hsl(244, 38%, 16%);
  --accent: hsl(277, 64%, 61%);
  --main-heading-stats-color: hsl(0, 0%, 100%);
  --main-paragraph-color: hsl(0, 0%, 100%);
  --stat-headings-color: hsla(0, 0%, 100%, 0.6);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--main-bg-color);
  font-family: var(--font-inter);
}

main {
  border: 5px solid yellow;
  padding: 5.3125rem 1.25rem;
  text-align: center;
}
@media (min-width: 40em) {
  main {
    padding: 10.9375rem 10.3125rem;
  }
}

h1,
h2,
h3 {
  margin-top: 0;
  color: var(--main-heading-stats-color);
}

p {
  color: var(--main-paragraph-color);
}

.highlighted {
  color: var(--accent);
}

.main-container {
  border: 2px solid red;
  margin: 0 auto;
  max-width: 20.625rem;
}
@media (min-width: 40em) {
  .main-container {
    max-width: 69.375rem;
  }
}

.img-header {
  border: 2px solid blue;
  height: 240px;
  background: url("../../images/image-header-mobile.jpg") var(--accent);
  background-size: cover;
  background-repeat: no-repeat;
  background-blend-mode: multiply;
}